<template>
  <div id="InboundMail">
    <el-menu style="" class="leftNav" unique-opened>
      <el-submenu
        :index="index + ''"
        v-for="(item, index) in leftNavData"
        :key="index"
      >
        <template slot="title">{{ index }}</template>
        <el-submenu
          :index="index2 + ''"
          v-for="(item2, index2) in item"
          :key="index2"
        >
          <template slot="title">{{ index2 }}</template>
          <el-menu-item
            :index="index2 + item3"
            v-for="(item3, index3) in item2"
            :key="index3"
            @click="NavClick(index, index2, item3)"
            >{{ item3 }}</el-menu-item
          >
        </el-submenu>
      </el-submenu>
    </el-menu>
    <div style="margin-left: 13vw; padding-top: 2vh">
      <h3 style="color: white">站外邮件回复</h3>
      <div class="topOpt" style="margin: 10px 0">
        <el-button type="primary" @click="noReply">全部未回复</el-button>
        <el-button type="primary" @click="replyData">已回复</el-button>
      </div>
      <el-table
        ref="InboundTable"
        highlight-current-row
        max-height="630"
        border
        :data="tableData"
        style="width: 99%"
      >
        <el-table-column
          align="center"
          type="index"
          :index="indexMethod"
          label="序号"
          width="60"
        >
        </el-table-column>
        <el-table-column
          prop="Inbox_email"
          align="center"
          label="收件邮箱地址"
          width="150"
        >
        </el-table-column>
        <el-table-column prop="email_date" align="center" label="收件时间">
        </el-table-column>
        <el-table-column
          prop="outbox_email"
          align="center"
          label="发件人邮箱地址"
        >
        </el-table-column>
        <el-table-column prop="platform" align="center" label="渠道" width="60">
        </el-table-column>
        <el-table-column prop="site" align="center" label="站点" width="60">
        </el-table-column>
        <el-table-column prop="country" align="center" label="国家" width="60">
        </el-table-column>
        <el-table-column prop="order_number" align="center" label="订单号">
        </el-table-column>
        <el-table-column
          prop="email_content"
          align="center"
          label="邮件内容"
          width="400"
        >
          <template slot-scope="scope">
            <el-input
              :rows="4"
              readonly
              type="textarea"
              v-model="scope.row.email_content"
            ></el-input>
          </template>
        </el-table-column>
        <el-table-column width="230" align="center" label="附件">
          <template slot-scope="scope">
            <div>
              <a
                v-for="(item2, index2) in scope.row.fileLI"
                :key="index2"
                download
                :href="item2"
                target="_Blank"
                >{{ getfilename(item2) }} <br
              /></a>
            </div>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="" label="回复情况（点击接取）">
          <template slot-scope="scope">
            <span
              v-if="scope.row.reply_situation == '未回复'"
              @click="
                showMail(
                  scope.row.email_content,
                  scope.row.fileLI,
                  scope.row.Inbox_email,
                  scope.row.outbox_email,
                  scope.row.platform,
                  scope.row.id
                )
              "
              style="
                color: skyblue;
                display: block;
                width: 100%;
                height: 94px;
                cursor: pointer;
                line-height: 94px;
              "
              >未回复</span
            >
            <span v-else>已回复</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="processing_time"
          width="80"
          label="处理时间"
        >
        </el-table-column>
        <el-table-column
          align="center"
          prop="dispose_person"
          width="80"
          label="处理人"
        >
        </el-table-column>
      </el-table>

      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="page"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pagetotle"
        style="margin: 1vh 0 0 35vw"
      >
      </el-pagination>
      <div class="bottomBox">
        <div style="position: absolute; top: 55vh; left: 41vw">
          <i
            class="el-icon-right"
            style="font-size: 2rem; color: #66b1ff; opacity: 0.5"
          ></i>
        </div>
        <div style="position: absolute; top: 84vh; left: 41vw">
          <i
            class="el-icon-right"
            style="font-size: 2rem; color: #66b1ff; opacity: 0.5"
          ></i>
        </div>
        <h4 style="margin: 2vh">回复站外邮件</h4>
        <div style="margin: 2vh 0 2vh 10vh">
          订单编号：<el-input
            v-model="replyOrderNo"
            placeholder="请输入订单编号"
            style="width: 20vw"
          ></el-input
          ><el-button
            type="primary"
            @click="getDataById()"
            style="margin-left: 1vw"
            >查 看</el-button
          >
        </div>
        <div>
          <span style="margin: 10vh 10vh 5vh 10vh"
            >站点：<el-input
              placeholder="请输入订单编号获取"
              v-model="replySite"
              style="width: 10vw"
              :readonly="editArr['replySite']"
            ></el-input
          ></span>
          <span style="margin: 5vh"
            >国家：<el-input
              placeholder="请输入订单编号获取"
              v-model="replyCountry"
              style="width: 10vw"
              :readonly="editArr['replyCountry']"
            ></el-input
          ></span>
          <span style="margin: 5vh"
            >平台：<el-input
              placeholder="请输入订单编号获取"
              v-model="replyPlatform"
              style="width: 10vw"
              :readonly="editArr['replyPlatform']"
            ></el-input
          ></span>
          <span style="margin: 5vh"
            >语言：<el-input
              placeholder="请输入订单编号获取"
              v-model="replyLanguage"
              style="width: 10vw"
              :readonly="editArr['replyLanguage']"
            ></el-input
          ></span>
          <div style="margin: 5vh 0 5vh 5vw">
            上传人：<el-input
              placeholder="请输入订单编号获取"
              v-model="replyUploadPeople"
              style="width: 10vw"
              readonly
            ></el-input>
          </div>
        </div>
        <div style="margin: 2vh 0 0 10vh">
          商品及其数量：<el-input
            placeholder="请输入订单编号获取"
            v-model="replyGoodsNo"
            style="width: 30vw"
            :readonly="editArr['replyGoodsNo']"
          ></el-input>
        </div>
        <div style="margin: 2vh 0 0 10vh; display: flex">
          <div style="flex: 1">
            买家消息内容：
            <el-input
              type="textarea"
              placeholder="请输入内容"
              v-model="replyBuysText"
              maxlength="9999"
              show-word-limit
              style="width: 20vw"
              :rows="15"
              readonly
            >
            </el-input>
          </div>
          <div style="flex: 1">
            <el-button
              type="primary"
              style="margin: 17.5vh 0 0 5vw; width: 10vw"
              @click="interpret"
              :disabled="interpretBtn"
              >匹配并翻译</el-button
            >
          </div>
          <div style="flex: 2">
            <el-input
              type="textarea"
              placeholder="买家消息内容翻译"
              v-model="replyTranslateChinese"
              maxlength="9999"
              show-word-limit
              style="width: 35vw"
              :rows="15"
            >
            </el-input>
          </div>
        </div>
        <div style="margin: 2vh 0 0 10vh; display: flex">
          <div style="flex: 4; border: 1px solid #dddddd">
            <ul class="textareaall">
              <li
                style="width: 50%"
                v-for="(item, index) in allTextarea"
                :key="index"
              >
                <h5 style="padding: 0.5vh; height: 5vh">
                  国家：{{ item.country }}---问题类型：{{ item.problem_type }}
                </h5>
                <div
                  style="width: 100%; height: 13vh; overflow-y: auto"
                  @click="alldiv(index)"
                  class="allDiv"
                >
                  {{ item.email_content }}
                </div>
              </li>
            </ul>
            <input
              type="checkbox"
              class="elseCheckbox"
              @click="elseCheckbox()"
            /><textarea
              name=""
              id=""
              cols="40"
              rows="8"
              class="elseTexarea"
              @blur="elseCheckbox()"
            ></textarea>
          </div>
          <div style="flex: 5">
            <el-input
              type="textarea"
              placeholder="模板内容翻译"
              v-model="replyTranslateChinese2"
              maxlength="9999"
              show-word-limit
              style="width: 35vw; margin: 0 0 0 4.5vw"
              :rows="7"
              readonly
            >
            </el-input>
          </div>
        </div>
        <div style="margin: 2vh 0 0 10vh; display: flex">
          <div style="flex: 4">
            <div style="margin-bottom: 2vh">
              邮件标题：<el-input
                placeholder=""
                v-model="replyEmailTitle"
                style="width: 20vw"
              ></el-input>
            </div>
            <div style="margin-bottom: 2vh">
              问题类型：
              <el-autocomplete
                class="inline-input"
                v-model="replyProblemType"
                :fetch-suggestions="querySearch"
                placeholder="请输入内容"
                style="width: 20vw"
              ></el-autocomplete>
              <!-- <el-input
                placeholder="请选择模板获取"
                v-model="replyProblemType"
                style="width: 20vw"
              ></el-input> -->
            </div>
            <div style="max-height: 15vh; overflow-y: auto">
              缺件及其数量
              <ul style="width: 100%" class="lessul">
                <li
                  style="width: 100%"
                  v-for="(item, index) in partData"
                  :key="index"
                >
                  <input
                    type="checkbox"
                    class="lackCheck"
                    @change="lackCheckChange"
                  /><span class="lessspan">{{ item.part_code }}</span
                  ><input
                    type="number"
                    value="1"
                    disabled
                    style="width: 40%; height: 2.5vh"
                    class="lackDo"
                  />
                </li>
              </ul>
            </div>
            <div style="max-height: 15vh; overflow-y: auto; margin-top: 2vh">
              破损及其数量
              <ul style="width: 100%" class="badul">
                <li
                  style="width: 100%"
                  v-for="(item, index) in partData"
                  :key="index"
                >
                  <input
                    type="checkbox"
                    @change="damageCheckChange"
                    class="damagedCheck"
                  /><span class="damagespan">{{ item.part_code }}</span
                  ><input
                    type="number"
                    value="1"
                    disabled
                    style="width: 40%; height: 2.5vh"
                    class="damagedDo"
                  />
                </li>
              </ul>
            </div>
            <div>
              相关操作:<select
                v-model="replyassociatedOperation"
                style="width: 15vw; height: 3vh"
              >
                <option value="发新品">发新品</option>
                <option value="退全款">退全款</option>
                <option value="部分退款">部分退款</option>
                <option value="退货">退货</option>
              </select>
            </div>
            <div>
              客户评级:<select
                v-model="replywhiteOrBlack"
                style="width: 15vw; height: 3vh"
              >
                <option value="白名单">白名单</option>
                <option value="黑名单">黑名单</option>
              </select>
            </div>
            <div>
              是否获得好评:<select
                v-model="replygoodORbad"
                style="width: 13vw; height: 3vh"
              >
                <option value="是">是</option>
                <option value="否">否</option>
              </select>
            </div>
          </div>
          <div style="flex: 6; margin-left: 14vw">
            附件：
            <!-- <a :href="fileUrl" download>{{ getfilename(fileUrl) }}</a> -->
            <a
              v-for="(item, index) in fileUrl"
              :key="index"
              :href="item"
              download
              target="_Blank"
              >{{ getfilename(item) }} <br
            /></a>
          </div>
        </div>
        <div style="margin: 2vh 0 0 10vh; display: flex; margin-bottom: 5vh">
          <div style="flex: 1">
            <el-button type="primary" @click="submitEmail()">提 交</el-button>
          </div>
          <div style="flex: 1">
            <el-button type="warning" @click="replacement()">重 置</el-button>
          </div>
          <div style="flex: 1">
            <el-button type="danger" @click="cancel()">取 消</el-button>
          </div>
          <div style="flex: 6"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      editArr: {
        replySite: true,
        replyCountry: true,
        replyPlatform: true,
        replyLanguage: true,
        replyGoodsNo: true
      }, // 是否可编辑
      replyGoodsNo: "", //回复邮件商品及其数量
      replyOrderNo: "", //回复邮件订单编号
      replySite: "", //回复邮件站点
      replyCountry: "", //回复邮件国家
      replyLanguage: "", //回复邮件语言
      replyBuysText: "", //买家消息内容
      replyTranslateChinese: "", //翻译成中文
      replyTranslateChinese2: "", //翻译成中文2
      replyProblemType: "", //回复邮件问题类型
      replyassociatedOperation: "", //回复文件相关操作
      replywhiteOrBlack: "", //回复邮件黑白名单
      replygoodORbad: "", //回复邮件是否好评
      replysku: "", //回复邮件SKU
      replyPlatform: "", //回复邮件平台
      replyUploadPeople: "", //回复邮件上传人
      replyProductName: "", //提交产品名
      replyProductCode: "", //提交产品编号
      replyEmailTitle: "", //提交的邮件标题

      total: 0,
      page: 1,
      leftNavData: [], //侧边导航栏数据
      firstNav: "", //一级导航
      twiceNav: "", //二级导航
      threeNav: "", //三级导航
      tableData: [], //表格数据
      allTextarea: [], //框框所有textarea
      productDo: "", //商品编码
      partData: [], //缺件和损坏
      fileUrl: "", //附件

      submitInEmail: "", //提交收件箱
      submitOutEmail: "", //提交发件箱
      submitId: "", //提交ID
      submitWarehouseCode: "", //提交仓库号
      situation: "", //未回复、已回复状态

      fileList: [], //文件存放数组

      interpretBtn: false, //匹配并翻译是否能按判断,
      restaurants: [] // 问题下拉列表，点击翻译后获取
    };
  },
  methods: {
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    querySearch(queryString, cb) {
      // restaurants 列表 [{value:'xx'}]
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    getDefaultData() {
      //获取默认数据
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: 1,
          type: "站外"
        }
      }).then(res => {
        console.log(res);
        // this.leftNavData = res.data.left_data_dict;
        this.tableData = res.data.re_data;
        this.total = res.data.count[0].count / 5;
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].accessory_addr) {
            this.tableData[i]["fileLI"] = this.tableData[
              i
            ].accessory_addr.split("@");
          }
        }
      });
    },
    indexMethod(index) {
      return index + 1 + (this.page - 1) * 50;
    },
    NavClick(index, index2, item3) {
      //点击三级导航栏事件
      this.firstNav = index;
      this.twiceNav = index2;
      this.threeNav = item3;
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: 1,
          type: "站外",
          platform: this.firstNav,
          country: this.twiceNav,
          site: this.threeNav
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.re_data;
        this.$nextTick(() => {
          this.$refs["InboundTable"].bodyWrapper.scrollTop = 0;
        });
        this.total = res.data.count[0].count / 5;
        console.log(this.tableData);
        this.page = 1;
        this.situation = "";
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].accessory_addr) {
            this.tableData[i]["fileLI"] = this.tableData[
              i
            ].accessory_addr.split("@");
          }
        }
      });
    },
    fileSubmit(ev) {
      //上传文件
      let file = ev.target.file;
      let formData = new FormData();
      for (let item of file) {
        formData.append("", item);
      }
    },
    damageCheckChange() {
      //判断是否有损坏件
      let damagedCheck = document.querySelectorAll(".damagedCheck");
      let damagedDo = document.querySelectorAll(".damagedDo");
      for (let i = 0; i < damagedCheck.length; i++) {
        if (damagedCheck[i].checked == true) {
          damagedDo[i].removeAttribute("disabled");
        } else {
          damagedDo[i].setAttribute("disabled", "disabled");
        }
      }
    },
    lackCheckChange() {
      //判断是否有缺件
      let lackCheck = document.querySelectorAll(".lackCheck");
      let lackDo = document.querySelectorAll(".lackDo");
      for (let i = 0; i < lackCheck.length; i++) {
        if (lackCheck[i].checked == true) {
          lackDo[i].removeAttribute("disabled");
        } else {
          lackDo[i].setAttribute("disabled", "disabled");
        }
      }
    },
    noReply() {
      //点击全部未回复
      this.situation = "未回复";
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: 1,
          type: "站外",
          platform: this.firstNav,
          country: this.twiceNav,
          site: this.threeNav,
          reply_situation: this.situation
        }
      }).then(res => {
        // this.leftNavData = res.data.left_data_dict;
        this.tableData = res.data.re_data;
        this.$nextTick(() => {
          this.$refs["InboundTable"].bodyWrapper.scrollTop = 0;
        });
        this.total = res.data.count[0].count / 5;
        console.log(this.tableData);
        this.page = 1;
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].accessory_addr) {
            this.tableData[i]["fileLI"] = this.tableData[
              i
            ].accessory_addr.split("@");
          }
        }
      });
    },
    replyData() {
      //点击已回复
      this.situation = "已回复";
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: 1,
          type: "站外",
          platform: this.firstNav,
          country: this.twiceNav,
          site: this.threeNav,
          reply_situation: this.situation
        }
      }).then(res => {
        console.log(res);
        // this.leftNavData = res.data.left_data_dict;
        this.tableData = res.data.re_data;
        this.$nextTick(() => {
          this.$refs["InboundTable"].bodyWrapper.scrollTop = 0;
        });
        this.total = res.data.count[0].count / 5;
        console.log(this.tableData);
        this.page = 1;
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].accessory_addr) {
            this.tableData[i]["fileLI"] = this.tableData[
              i
            ].accessory_addr.split("@");
          }
        }
      });
    },
    getfilename(x) {
      // console.log("打印文件名", x);
      //获取文件名函数
      let pos = x.lastIndexOf("/");
      return x.substring(pos + 1);
    },
    showMail(message, file, inemail, outemail, platform, id) {
      //打开下方的图表并赋值给买家信息

      document.querySelector(".bottomBox").style.display = "block";
      this.replyBuysText = message;
      this.fileUrl = file;
      this.submitInEmail = inemail;
      this.submitOutEmail = outemail;
      this.submitId = id;
      this.replyOrderNo = "";
      this.replySite = "";
      this.replyCountry = "";
      this.replyPlatform = "";
      this.replyLanguage = "";
      // this.replyUploadPeople = ''
      this.replyGoodsNo = "";
      this.replyTranslateChinese = "";
      this.replyTranslateChinese2 = "";
      this.allTextarea = [];
    },
    // showMail(message, file, inemail, outemail, platform, id) {
    //   //打开下方的图表并赋值给买家信息
    //   document.querySelector(".bottomBox").style.display = "block";
    //   this.replyBuysText = message;
    //   this.fileUrl = file;
    //   this.submitInEmail = inemail;
    //   this.submitOutEmail = outemail;
    //   this.submitId = id;
    //   this.replyOrderNo = "";
    //   this.replySite = "";
    //   this.replyCountry = "";
    //   this.replyPlatform = "";
    //   this.replyLanguage = "";
    //   // this.replyUploadPeople = ''
    //   this.replyGoodsNo = "";
    //   this.replyTranslateChinese = "";
    //   this.replyTranslateChinese2 = "";
    //   this.allTextarea = [];
    // },
    getDataById() {
      //根据订单编号获取数据并赋值
      axios({
        url: this._url2 + "server/customer_management/order_num_inquire/",
        method: "get",
        params: {
          order_num: this.replyOrderNo
        }
      }).then(res => {
        // 清除数据
        this.replySite = "";
        this.replyCountry = "";
        this.replyPlatform = "";
        this.replyLanguage = "";
        this.replyGoodsNo = "";

        // 还原为只读
        for (let key in this.editArr) {
          this.editArr[key] = true;
        }
        if (res.data.data.length > 0) {
          this.replySite = res.data.data[0].site;
          // this.replySite =
          //   this.replySite.charAt(0).toUpperCase() + this.replySite.slice(1);
          for (let key in res.data.country_dict) {
            if (res.data.data[0].country_code == key) {
              this.replyCountry = res.data.country_dict[key][0];
              this.replyLanguage = res.data.country_dict[key][1];
              //     this.productDo = res.data.data[0].product_code;
              this.replyGoodsNo =
                res.data.data[0].product_name +
                "        数量:" +
                res.data.data[0].quantity;
              this.replysku = res.data.data[0].sku;
              this.replyPlatform = res.data.data[0].platform;
              this.replyProductName = res.data.data[0].product_name;
              this.partData = res.data.commodity_data;
              // this.replyProductCode = res.data.data[0].product_code;
              //     this.submitWarehouseCode = res.data.data[0].warehouse_code;
              // 判断部分字段是否为空，如是，设置可编辑
              // if(!this.replySite){

              // }
              this.interpret();
              this.reflash();
            }
          }
        }
        if (!this.replySite) {
          this.editArr["replySite"] = false;
        }
        if (!this.replyCountry) {
          this.editArr["replyCountry"] = false;
        }
        if (!this.replyPlatform) {
          this.editArr["replyPlatform"] = false;
        }
        if (!this.replyLanguage) {
          this.editArr["replyLanguage"] = false;
        }
        if (!this.replyGoodsNo) {
          this.editArr["replyGoodsNo"] = false;
        }
      });
    },
    interpret() {
      //匹配并翻译
      this.interpretBtn = true;
      console.log(this.productDo);
      let formData = new FormData();
      formData.append("platform", this.replyPlatform);
      formData.append("country", this.replyCountry);
      formData.append("language", this.replyLanguage);
      formData.append("content", this.replyBuysText);
      formData.append("product_code", this.productDo);
      this.$axios
        .post(
          this._url2 +
            "server/customer_management/translate_semantic_analysis/",
          formData
        )
        .then(res => {
          this.replyTranslateChinese = res.data.data;
          console.log(res, 99);
          this.allTextarea = res.data.tem_data;
          // console.log(this.allTextarea, 123);
          this.interpretBtn = false;
          // 处理问题类型下拉列表
          let problemList = res.data.re_problem_data || [];
          this.restaurants = [];
          for (let key in problemList) {
            let item = {};
            let value = problemList[key];
            item.value = value.problem_type;
            this.restaurants.push(item);
          }
        });
    },
    elseCheckbox() {
      //判断有没有勾选
      if (document.querySelector(".elseCheckbox").checked == true) {
        let str = document.querySelector(".elseTexarea").value;
        if (!str.trim().length) {
          console.log(str.trim());
          return;
        }
        console.log("发起请求？？？");
        axios({
          url: this._url2 + "server/customer_management/re_message_translate/",
          method: "get",
          params: {
            content: str
          }
        }).then(res => {
          // this.replyTranslateChinese2 = res.data.re_content;

          if (res.data.re_content.length > 0) {
            this.replyTranslateChinese2 = res.data.re_content[0];
          } else {
            this.replyTranslateChinese2 = "";
          }
          console.log(res);
        });
        this.replyProblemType = "";
        let allDiv = document.querySelectorAll(".allDiv");
        for (let i = 0; i < allDiv.length; i++) {
          allDiv[i].style.backgroundColor = "";
        }
        console.log(str);
      }
    },
    submitEmail() {
      console.log("问题类型", this.replyProblemType);
      let flag = 0; //判断勾选的input框是否有值
      let damagedDo = document.querySelectorAll(".damagedDo");
      let damagedCheck = document.querySelectorAll(".damagedCheck");
      let damagespan = document.querySelectorAll(".damagespan");
      let lackCheck = document.querySelectorAll(".lackCheck");
      let lackDo = document.querySelectorAll(".lackDo");
      let lessspan = document.querySelectorAll(".lessspan");
      let demo = [];
      let demo2 = [];
      let str = "";
      for (let i = 0; i < damagedCheck.length; i++) {
        if (damagedCheck[i].checked == true) {
          let demo3 = [];
          demo3.push(damagespan[i].innerHTML);
          demo3.push(damagedDo[i].value);
          demo.push(demo3);
          if (damagedDo[i].value == "") {
            flag++;
          }
        }
      }
      for (let i = 0; i < lackCheck.length; i++) {
        if (lackCheck[i].checked == true) {
          let demo4 = [];
          demo4.push(lessspan[i].innerHTML);
          demo4.push(lackDo[i].value);
          demo2.push(demo4);
          if (lackDo[i].value == "") {
            flag++;
          }
        }
      }
      if (flag >= 1) {
        alert("请把勾选的缺件破损零件数量填上或者不勾选");
      } else {
        let allDiv = document.querySelectorAll(".allDiv");
        for (let i = 0; i < allDiv.length; i++) {
          if (allDiv[i].style.backgroundColor == "skyblue") {
            str = allDiv[i].innerHTML;
          }
        }
        if (document.querySelector(".elseCheckbox").checked == true) {
          str = document.querySelector(".elseTexarea").value; //如果其他的按钮被选中提交其他里的信息
        }
        if (str === "") {
          alert("请把选择回复模板或手动输入并打钩");
        } else {
          let formData = new FormData();
          formData.append("product_code", this.replyProductCode);
          formData.append("product_name", this.replyGoodsNo);
          formData.append("Inbox_email", this.submitInEmail);
          formData.append("outbox_email", this.submitOutEmail);
          formData.append("order_number", this.replyOrderNo);
          formData.append("problem_type", this.replyProblemType);
          formData.append("related_operations", this.replyassociatedOperation);
          formData.append("praise", this.replygoodORbad);
          formData.append("customer_status", this.replywhiteOrBlack);
          formData.append("lack_part", demo2);
          formData.append("damaged", demo);
          formData.append("email_template", str);
          formData.append("email_content", this.replyBuysText);
          formData.append("customer_translation", this.replyTranslateChinese);
          formData.append("translation_template", this.replyTranslateChinese2);
          formData.append("email_title", this.replyEmailTitle);
          let fileList = "";
          if (this.fileUrl) {
            for (let i = 0; i < this.fileUrl.length; i++) {
              fileList += this.fileUrl[i] + "@";
            }
            if (fileList) {
              fileList = fileList.substr(0, fileList.length - 1);
            }
          }
          formData.append("attachment_address", fileList);
          formData.append("sku", this.replysku);
          formData.append("upload_people", this.replyUploadPeople);
          formData.append("id", this.submitId);
          // formData.append("warehouse_code", this.submitWarehouseCode);
          let str2 =
            this.replyPlatform.charAt(0).toUpperCase() +
            this.replyPlatform.slice(1);

          formData.append("platform", str2);
          formData.append("country", this.replyCountry);
          formData.append("site", this.replySite);
          // console.log(formData,123123)
          // this.$axios
          //   .post(
          //     this._url2 + "server/customer_management/customer_reply_email/",
          //     formData
          //   )
          //   .then(res => {
          //     if (res.data.code == 200) {
          //       alert("提交成功");
          //       document.querySelector(".bottomBox").style.display = "none";
          //     }
          //   });
        }
      }
    },
    alldiv(index) {
      //模板变色
      if (document.querySelector(".elseCheckbox").checked == false) {
        let allDiv = document.querySelectorAll(".allDiv");
        for (let i = 0; i < allDiv.length; i++) {
          allDiv[i].style.backgroundColor = "";
        }
        allDiv[index].style.backgroundColor = "skyblue";
        this.replyTranslateChinese2 = this.allTextarea[index].email_translation;
        this.replyProblemType = this.allTextarea[index].problem_type;
      }
    },
    // 点击查看重置卖家消息内容以下内容
    reflash() {
      this.replyassociatedOperation = ""; // 相关操作
      this.replywhiteOrBlack = ""; //  客户评级
      this.replygoodORbad = ""; // 是否获得好评
      this.replyGoodsNo = ""; //商品及其数量
      this.replyTranslateChinese = ""; //买家消息内容翻译
      this.replyTranslateChinese2 = ""; //模板内容翻译
      this.replyEmailTitle = ""; // 邮件标题
      this.replyProblemType = ""; // 问题类型
      // this.replyBuysText = ""; // 买家消息内容
      //缺件及其数量 //破损及其数量
      document.querySelector(".elseTexarea").value = "";
    },
    replacement() {
      //重置按钮
      this.replyassociatedOperation = "";
      this.replywhiteOrBlack = "";
      this.replygoodORbad = "";
      this.replyOrderNo = "";
      this.replySite = "";
      this.replyCountry = "";
      this.replyPlatform = "";
      this.replyLanguage = "";
      this.replyUploadPeople = "";
      // this.replyGoodsNo = "";
      this.replyTranslateChinese = "";
      this.replyTranslateChinese2 = "";
      document.querySelector(".elseTexarea").value = "";
    },
    cancel() {
      //取消按钮
      document.querySelector(".bottomBox").style.display = "none";
    },
    handleCurrentChange(newPage) {
      // 页码切换会触发此函数
      if (this.situation == "") {
        this.situation = null;
      }
      this.page = newPage;
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: this.page,
          type: "站外",
          platform: this.firstNav,
          country: this.twiceNav,
          site: this.threeNav,
          reply_situation: this.situation
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.re_data;

        this.$nextTick(() => {
          this.$refs["InboundTable"].bodyWrapper.scrollTop = 0;
        });
        this.total = res.data.count[0].count / 5;
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].accessory_addr) {
            this.tableData[i]["fileLI"] = this.tableData[
              i
            ].accessory_addr.split("@");
          }
        }
      });
      if (this.situation == null) {
        this.situation = "";
      }
    }
  },
  created() {
    this.getDefaultData();
    axios({
      //左侧导航栏（权限用）
      url: this._url2 + "server/personnel_management/area_sign2/",
      method: "get"
    }).then(res => {
      console.log(res, 5);
      this.leftNavData = res.data.data;
    });
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    this.replyUploadPeople = sessionStorage.getItem("user_name");
  },
  watch: {}
};
</script>
<style>
