#InboundMail .el-textarea textarea:focus {
  background-color: #f5f7fa;
}
#InboundMail .el-textarea textarea,
#InboundMail .el-textarea textarea:hover,
#InboundMail .el-textarea textarea:focus {
  border: none;
  resize: none;
  overflow-x: hidden;
  min-height: 50px;
}
